.Header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: red;
}

.header {
  display: table;
  width: 100%;
  padding: 10px;
  height: 60px;
  background: #1b262c;
}

.header_title {
  font-size: 30px;
  color: white;
  font-weight: bold;
  text-align: left;
  display: table-cell;
  width: 80%;
  vertical-align: middle;
  padding-left: 30px;
}

.header_search {
  display: table-cell;
  vertical-align: middle;
}

.container-1 {
  width: 300px;
  vertical-align: middle;
  white-space: nowrap;
  position: relative;
}

.container-1 input#search {
  width: 200px;
  height: 30px;
  background: #2b303b;
  border: none;
  font-size: 10pt;
  color: #63717f;
  padding-left: 45px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  font-weight: bold;
  padding-right: 10px;
}

.container-1 input#search::-webkit-input-placeholder {
  color: #65737e;
}

.container-1 input#search:-moz-placeholder {
  /* Firefox 18- */
  color: #65737e;
}

.container-1 input#search::-moz-placeholder {
  /* Firefox 19+ */
  color: #65737e;
}

.container-1 input#search:-ms-input-placeholder {
  color: #65737e;
}

.container-1 input#search:focus {
  outline: 0;
}

.container-1 .icon {
  position: absolute;
  margin-top: 5px;
  margin-left: 10px;
  z-index: 1;
  color: #4f5b66;
  font-weight: bold;
}
