.filter_container {
  width: 100%;
  height: 30px;
  display: table;
}

.filter_title {
  display: table-cell;
  vertical-align: middle;
  justify-content: center;
  background: #1b262c;
  font-size: 15px;
  width: 120px;
  color: white;
  text-align: left;
  padding: 5px;
  padding-left:50px;
  padding-right:20px;
}

.filter_contents {
  display: table-cell;
  background: #0f4c75;
  text-align: left;
  color: white;
  padding: 5px;
  vertical-align: middle;
  padding-top: 7px;
  padding-bottom: 7px;
}

.filter_contents_children {
  display: table-cell;
  padding-left: 10px;
  padding-right: 10px;
}

.filter_button {
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 7px;
  border: 0px solid;
  height: 25px;
  background: #3282b8;
  color: black;
}

.filter_button:hover {
  background: #bbe1fa;
}

.filter_button:focus {
  outline: 0;
}

.filter_button:active {
  background: white;
}
